.weather-container {
  width: auto;
  max-width: 260px;
  max-height: 600px;
  margin: 0 auto;
  padding: 2rem 2.5rem;
  padding-bottom: 7rem;
  border-radius: 0.5rem;
  background: rgba(170, 170, 170, 0.1);
  outline: solid 1px rgba(0, 0, 0, 0.1);
  outline-offset: 2px;
}

.container-top {
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(-5%);
}

.container-top h1 {
  color: black;
  font-size: 1em;
}

.weather-title {
  padding: 20px;
  margin-bottom: -50px;
  display: flex;
}

.paragraph {
  color: black;
}

.container-mid {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -20px;
}

.tijd-main {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.1rem;
  padding: 20px;
  width: 100%;
}

.tijd {
  color: black;
  padding: 20px;
}

.weather-temp-main {
  font-weight: bold;
  font-size: 2rem;
  color: black;
  margin-right: 20px;
}

.weather-tepm {
  vertical-align: text-top;
  font-size: 1rem;
  color: #9b9b9b;
  font-weight: bold;
}

.weather-icon-location {
  font-size: 2rem;
  padding-bottom: 5px;
  padding-right: 5px;
  color: rgb(102, 102, 102);
}

.container-bot {
  display: flex;
  align-items: center;
}

.container-map {
  margin-top: -15px;
  display: flex;
  align-items: center;
  justify-content: center;
}
