.container-form {
  display: flex;
  margin: 2rem 0;
  align-items: center;
  justify-content: center;
}

.input {
  background-color: #fff;
  padding: 0.8rem;
}

.weatherSearch {
  margin: 0 15px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  outline: none;
  border: none;
  border-bottom: solid 1px rgba(0, 0, 0, 0.3);
  width: 220px;
  text-align: left;
}

.weatherSearch::placeholder {
  color: rgb(0, 0, 0);
  font-size: 12px;
  font-weight: 500;
}

.weatherIcon {
  color: black;
  width: 25px;
  height: 25px;
}

.container-mid {
  margin: 3rem 0;
}
